  body.node--type-landing.js-off-canvas-dialog-open #drupal-off-canvas .ui-autocomplete li {
    background: #fff; }
  body.node--type-landing.js-off-canvas-dialog-open #drupal-off-canvas .ui-autocomplete li a {
