body .user-logged-in {
  display: none; }

body .user-not-logged-in {
  display: block; }

body.user-logged-in .user-logged-in {
  display: block; }

body.user-logged-in .user-not-logged-in {
  display: none; }

#user-login {
  padding: 40px 0;
  margin: 0 auto; }
  #user-login #user-login-content {
    background-color: #eee;
    padding: 30px 50px;
    text-align: left; }
    #user-login #user-login-content .main-content {
      float: none; }
  #user-login .user-login-form {
    display: block;
    margin-bottom: 20px;
    width: 420px; }
    @media (max-width: 700px) {
      #user-login .user-login-form {
        width: auto; } }
    #user-login .user-login-form label {
      font-weight: 600; }
    #user-login .user-login-form .description {
      color: #3e3e3e;
      font-style: italic;
      line-height: 1.2em;
      margin-top: 5px; }
    #user-login .user-login-form .simplesamlphp-auth-login-link {
      color: #217570; }
    #user-login .user-login-form input[type="submit"] {
      cursor: pointer; }

